export default {
	data() {
		return {};
	},
	methods: {
		onSubmit(e) {
			this.$Fetch({
				url: 'm_Settings/saveComplaint',
				data: e.detail.value,
				success: res => {
					uni.showToast({
						title: '提交成功！',
						icon:"success"
					});
				}
			});
		}
	}
};
